import type { State } from '../fork/getPathFromState';

export function getSimplePathDataFromState(state: State): {
  pathname: string;
  params: Record<string, string>;
} {
  const segments: string[] = [];
  const params: Record<string, string> = {};

  let current: State | undefined = state;

  while (current) {
    const currentRoute = current.routes?.[current.index ?? 0];
    if (currentRoute) {
      if (currentRoute.name) {
        segments.push(currentRoute.name);
      }
      if (currentRoute.params) {
        for (const key in currentRoute.params) {
          const value = currentRoute.params?.[key as keyof typeof currentRoute.params];
          if (value) {
            params[key] = value;
          }
        }
      }
      current = currentRoute.state;
    }
  }

  return {
    pathname: '/' + segments.join('/'),
    params,
  };
}
